<template>
      <!-- 添加购物车 -->
  <add-cart ref="addCart"> </add-cart>
</template>

<script>

export default {
  name: "addCartView",
  data() {
    return {
      proDetail:null
    };
  },
  computed: {},
  mounted() {
    if(this.$route.query.proDetail){
       this.proDetail=JSON.parse(this.$route.query.proDetail);
       this.$refs.addCart.load(this.proDetail,true);
    }
  }
}

</script>


